import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function daysOfSalesOutstandingTtm(tableName: string) {
  const title = "Days Sales Outstanding (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["daysOfSalesOutstandingTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Days to collect customer payments.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about days sales outstanding or other operational efficiency metrics?`,
              value:
                info.row.original.financialData.companyKeyMetrics?.data?.daysOfSalesOutstandingTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.daysOfSalesOutstandingTtm}
        />
      )
    },
  })
}

export { daysOfSalesOutstandingTtm }
